.Data{
    width: 100%;
    margin: 30px 0;
    overflow-x: scroll;
}

.Data h2{
    margin : 20px 0;
}

.tableStyle{
    width: 100%;
    text-align: center;
}

.tableStyle table{
    width: 100%;
    border-collapse: collapse;
}

.tableStyle table td, .tableStyle table th {
    border: 1px solid #ddd;
    padding: 8px;
    font-size: 13px;
}
.tableStyle table tr:nth-child(even){background-color: #f2f2f2;}

.tableStyle table tr:hover {background-color: #ddd;}

.tableStyle table th {
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: left;
    background-color:  #1f25d1;
    color: white;
  }